.has-error {
    label
    textarea,
    textarea.form-control,
    textarea + #cke_content,
    select,
    input {
        border-color: $danger;
    }
    label {
        color: $danger;
    }
}

#cke_content {
    width: 98.5%;
}

#publish_date,
#publish_time {
    cursor: pointer;
}

.data-style {
    font-family: monospace;
    font-size: .7em;
}
.input-group-append {
    .btn {
        margin: 0;
        line-height: 1;
        padding-top: 1em;
    }
}
.input-group {
    .form-control + .input-group-append {
        .input-group-text {
            padding: 11px 10px 12px 0;
        }
    }
}
#page-image-preview {
    @media(min-width: 767px) {
        max-height: 40px;
    }
}



















.input-group select.form-control:first-child {
    -webkit-appearance: none;
    -moz-appearance: none;
    background-position: right 50%;
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
    padding: .5em;
    padding-right: 1.5em;
    @include vendor(border-radius, 4px 0 0 4px !important);
}

form {
    .active {
        color: green;
    }
    &.form-inline {
        display: inline-block;
    }
}

.switch {
    cursor: pointer;

    .fa {
        vertical-align: -5px;
        margin-right: .25em;
    }

    .active {
        display: none;
    }

    input[type="radio"] {
        position: relative;
        z-index: -1;
    }
    &.checked {
        .active {
            display: inline-block;
        }
        .inactive {
            display: none;
        }
    }
}

.disabled {
    color: #dcdbdb;
    pointer-events: none;
    cursor: not-allowed;

    .switch span.active {
        color: #dcdbdb;
        pointer-events: none;
        cursor: not-allowed;
    }
}

.custom-checkbox-fa {
    input[type="checkbox"]  {
        opacity: 0;
    }
}


label {

    input[type="radio"] ~ i.fa.fa-circle-o{
        color: #c8c8c8;    display: inline;
    }
    input[type="radio"] ~ i.fa.fa-dot-circle-o{
        display: none;
    }
    input[type="radio"]:checked ~ i.fa.fa-circle-o{
        display: none;
    }
    input[type="radio"]:checked ~ i.fa.fa-dot-circle-o{
        color: #7AA3CC;    display: inline;
    }

    input[type="checkbox"] ~ i.fa.fa-square-o{
        color: #c8c8c8;    display: inline;
    }
    input[type="checkbox"] ~ i.fa.fa-check-square-o{
        display: none;
    }
    input[type="checkbox"]:checked ~ i.fa.fa-square-o{
        display: none;
    }
    input[type="checkbox"]:checked ~ i.fa.fa-check-square-o{
        color: #7AA3CC;    display: inline;
    }

    input[type="checkbox"]:checked ~ i.fa.fa-check-square-o {
        color: $danger !important;
        display: inline;
        left: 2px;
    }

    &:hover {
        input[type="radio"] ~ i.fa {
            color: #7AA3CC;
        }

        input[type="checkbox"] ~ i.fa {
            color: #7AA3CC;
        }
    }

}

div[data-toggle="buttons"] {
    label {
        &:active,
        &.active {
            @include vendor(box-shadow, none);
        }
    }
}

.btn.no-shadow .fa {
    float: left;
    left: 0;
    position: absolute;
    width: 40px;
}

#notes {
    border-right: none;
    border-radius: 3px 0 0 3px;
}



